import React, { Component } from 'react'
import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import Axios from '../../../lib/api';
import swal from 'sweetalert';
class Register extends Component {

  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      email: '',
      password: '',
      password2: '',
    }
  }

  onEmailChange(e) {
    this.setState({ email: e.target.value })
  }
  onPasswordChange(e) {
    this.setState({ password: e.target.value })
  }
  onPassword2Change(e) {
    this.setState({ password2: e.target.value })
  }
  onTapRegister() {


    this.setState({ loading: true })
    var { email, password } = this.state;
    Axios.post('users/signup', { email: email, password: password, password2: password }).then(r => {
      console.log(r.data)
      swal({
        title: "Successfully created.",
        text: "Check your email",
        icon: "success",
        buttons: {
          confirm: {
            text: "OK",
            value: true,
            visible: true,
            className: "",
            closeModal: true
          },
        }
        
      })

      this.setState({ loading: false })
    })
      .catch(e => {
        console.log(e)
        this.setState({ loading: false })
      })
  }
  render() {

    var { loading, password, password2 } = this.state;
    var enableSubmit = password == password2 && password.length
    return (
      <div className="c-app c-default-layout flex-row align-items-center">
        <CContainer>
          <CRow className="justify-content-center">
            <CCol md="9" lg="7" xl="6">
              <CCard className="mx-4">
                <CCardBody className="p-4">
                  <CForm>
                    <h1>Register</h1>
                    <p className="text-muted">Create your account</p>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>@</CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="text" placeholder="Email" autoComplete="email" value={this.state.email} onChange={(e) => this.onEmailChange(e)} />
                    </CInputGroup>
                    <CInputGroup className="mb-3">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="password" placeholder="Password" autoComplete="new-password" value={this.state.password} onChange={(e) => this.onPasswordChange(e)} />
                    </CInputGroup>
                    <CInputGroup className="mb-4">
                      <CInputGroupPrepend>
                        <CInputGroupText>
                          <CIcon name="cil-lock-locked" />
                        </CInputGroupText>
                      </CInputGroupPrepend>
                      <CInput type="password" placeholder="Repeat password" autoComplete="new-password" value={this.state.password2} onChange={(e) => this.onPassword2Change(e)} />
                    </CInputGroup>
                    <CButton color="success" block onClick={() => { this.onTapRegister(); }}>Create Account
                     {loading && <span className="spinner-border spinner-border-sm ml-1" role="status" aria-hidden="true"></span>}
                    </CButton>
                  </CForm>
                </CCardBody>
                {/* <CCardFooter className="p-4">
                <CRow>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-facebook mb-1" block><span>facebook</span></CButton>
                  </CCol>
                  <CCol xs="12" sm="6">
                    <CButton className="btn-twitter mb-1" block><span>twitter</span></CButton>
                  </CCol>
                </CRow>
              </CCardFooter> */}
              </CCard>
            </CCol>
          </CRow>
        </CContainer>
      </div>
    )
  }
}

export default Register
